import React from "react";
import { makeStyles } from '@material-ui/core/styles';
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";

const useStyles = makeStyles((theme) => ({
    root: {
        display : 'flex',
        flexDirection : 'column',
        alignItems : 'center',
        width : 450,
        margin : 'auto',
        padding : theme.spacing(),
    },
    form : {
        display : 'flex',
        flexDirection : 'column',
        alignItems : 'center',
        padding: theme.spacing(),
        width : '-webkit-fill-available',
    },
    space : {
        marginBottom: theme.spacing(),
    }
}));

export default function Footer() {
    const classes = useStyles();

    return (
        <Paper className={classes.root}>
            <Typography variant={"h4"} className={classes.space}>Add Employee</Typography>
            <form noValidate autoComplete={"off"} className={classes.form}>
                <TextField fullWidth className={classes.space} required id={"firstName"} label={"First Name"}/>
                <TextField fullWidth className={classes.space} required id={"lastName"} label={"Last Name"}/>
                <TextField fullWidth className={classes.space} required id={"emailId"} label={"Email Address"}/>
                <Button
                    variant={"contained"}
                    color={"primary"}
                    className={classes.space}
                >
                    Add
                </Button>
            </form>
        </Paper>
    )
}




